"use client";
import { useState, useEffect } from 'react';

const Register = () => {
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    firstname: '',
    fullname: '',
    lastname: '',
    address: '',
    sex: '',
    birthday: '',
    acceptTerms: false
  });

  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [isClient, setIsClient] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState({
    score: 0,
    feedback: '',
    color: '#dc2626',
    strengthText: ''
  });

  // Fix hydration mismatch by ensuring client-side rendering
  useEffect(() => {
    setIsClient(true);
  }, []);

  const validatePasswordStrength = (password) => {
    if (!password) {
      setPasswordStrength({ 
        score: 0, 
        feedback: '', 
        color: '#dc2626',
        strengthText: ''
      });
      return;
    }

    let score = 0;
    let feedback = [];
    
    // Length check
    if (password.length >= 8) score += 1;
    else feedback.push('อย่างน้อย 8 ตัวอักษร');
    
    // Character variety checks
    if (/[a-z]/.test(password)) score += 1;
    else feedback.push('ตัวพิมพ์เล็ก');
    
    if (/[A-Z]/.test(password)) score += 1;
    else feedback.push('ตัวพิมพ์ใหญ่');
    
    if (/[0-9]/.test(password)) score += 1;
    else feedback.push('ตัวเลข');
    
    if (/[^A-Za-z0-9]/.test(password)) score += 1;
    else feedback.push('อักขระพิเศษ');

    let strengthText = '';
    let color = '#dc2626';
    
    if (score <= 2) {
      strengthText = 'อ่อนมาก';
      color = '#dc2626';
    } else if (score === 3) {
      strengthText = 'ปานกลาง';
      color = '#f59e0b';
    } else if (score === 4) {
      strengthText = 'ดี';
      color = '#ef4444';
    } else {
      strengthText = 'แข็งแกร่งมาก';
      color = '#10b981';
    }

    setPasswordStrength({
      score: score,
      feedback: feedback.length > 0 ? `ขาด: ${feedback.join(', ')}` : 'รหัสผ่านแข็งแกร่ง!',
      color: color,
      strengthText: strengthText
    });
  };

  const hashPassword = async (plainPassword) => {
    try {
      // Using a simple hash for demo - in real app use proper bcrypt
      const encoder = new TextEncoder();
      const data = encoder.encode(plainPassword + 'salt123');
      const hashBuffer = await crypto.subtle.digest('SHA-256', data);
      const hashArray = Array.from(new Uint8Array(hashBuffer));
      const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
      return hashHex;
    } catch (error) {
      console.error('Error hashing password:', error);
      throw new Error('ไม่สามารถเข้ารหัสรหัสผ่านได้');
    }
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));

    // Special handling for password field
    if (name === 'password') {
      validatePasswordStrength(value);
    }

    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    // Username validation
    if (!formData.username) {
      newErrors.username = 'กรุณากรอกชื่อผู้ใช้';
    } else if (formData.username.length < 3) {
      newErrors.username = 'ชื่อผู้ใช้ต้องมีอย่างน้อย 3 ตัวอักษร';
    }
    
    // Password validation - Enhanced for security
    if (!formData.password) {
      newErrors.password = 'กรุณากรอกรหัสผ่าน';
    } else if (formData.password.length < 8) {
      newErrors.password = 'รหัสผ่านต้องมีอย่างน้อย 8 ตัวอักษร';
    } else if (passwordStrength.score < 3) {
      newErrors.password = 'รหัสผ่านยังไม่ปลอดภัยเพียงพอ กรุณาใช้รหัสผ่านที่แข็งแกร่งขึ้น';
    }
    
    if (!formData.firstname) newErrors.firstname = 'กรุณาเลือกคำนำหน้าชื่อ';
    if (!formData.fullname) newErrors.fullname = 'กรุณากรอกชื่อ';
    if (!formData.lastname) newErrors.lastname = 'กรุณากรอกนามสกุล';
    if (!formData.address) newErrors.address = 'กรุณากรอกที่อยู่';
    if (!formData.sex) newErrors.sex = 'กรุณาเลือกเพศ';
    if (!formData.birthday) newErrors.birthday = 'กรุณาเลือกวันเกิด';
    if (!formData.acceptTerms) newErrors.acceptTerms = 'กรุณายอมรับเงื่อนไข';

    // Age validation
    if (formData.birthday) {
      const today = new Date();
      const birthday = new Date(formData.birthday);
      let age = today.getFullYear() - birthday.getFullYear();
      const monthDiff = today.getMonth() - birthday.getMonth();
      
      if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birthday.getDate())) {
        age--;
      }
      
      if (age < 13) {
        newErrors.birthday = 'อายุต้องมากกว่า 13 ปี';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const resetForm = () => {
    setFormData({
      username: '',
      password: '',
      firstname: '',
      fullname: '',
      lastname: '',
      address: '',
      sex: '',
      birthday: '',
      acceptTerms: false
    });
    setErrors({});
    setPasswordStrength({
      score: 0,
      feedback: '',
      color: '#dc2626',
      strengthText: ''
    });
    setShowPassword(false);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const isValid = validateForm();
    if (!isValid) {
      alert('ข้อมูลไม่ครบถ้วน! กรุณากรอกข้อมูลให้ครบถ้วนและถูกต้อง');
      return;
    }

    // Check password strength one more time
    if (passwordStrength.score < 3) {
      const confirm = window.confirm('รหัสผ่านของคุณยังไม่ปลอดภัยเพียงพอ คุณต้องการดำเนินการต่อหรือไม่?');
      if (!confirm) {
        return;
      }
    }

    setIsLoading(true);

    try {
      // Hash password before sending
      console.log('Hashing password...');
      const hashedPassword = await hashPassword(formData.password);
      console.log('Password hashed successfully');

      const requestData = {
        username: formData.username,
        password: hashedPassword,
        firstname: formData.firstname,
        fullname: formData.fullname,
        lastname: formData.lastname,
        address: formData.address,
        sex: formData.sex,
        birthday: formData.birthday,
      };

      console.log('Request payload (password hidden for security):', {
        ...requestData,
        password: '[HASHED PASSWORD]'
      });

      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      console.log('Registration successful');
      
      alert('สมัครสมาชิกสำเร็จ! คุณสามารถเข้าสู่ระบบได้แล้ว รหัสผ่านของคุณถูกเข้ารหัสอย่างปลอดภัยแล้ว');

      // Reset form after successful registration
      resetForm();

    } catch (error) {
      console.error('Registration error:', error);
      
      let errorMessage = 'ไม่สามารถสมัครสมาชิกได้ กรุณาลองใหม่อีกครั้ง';
      
      if (error.message.includes('duplicate') || error.message.includes('already exists')) {
        errorMessage = 'ชื่อผู้ใช้นี้มีอยู่แล้ว กรุณาเลือกชื่อผู้ใช้อื่น';
      } else if (error.message.includes('network') || error.message.includes('fetch')) {
        errorMessage = 'ไม่สามารถเชื่อมต่อกับเซิร์ฟเวอร์ได้ กรุณาตรวจสอบการเชื่อมต่ออินเทอร์เน็ต';
      } else if (error.message.includes('เข้ารหัสรหัสผ่าน')) {
        errorMessage = 'เกิดข้อผิดพลาดในการประมวลผลรหัสผ่าน กรุณาลองใหม่';
      }

      alert(`เกิดข้อผิดพลาด! ${errorMessage}`);
    } finally {
      setIsLoading(false);
    }
  };

  // Pre-defined particle positions to avoid hydration mismatch
  const particles = [
    { id: 1, left: '10%', top: '20%', size: 3, delay: 2.1, color: '#dc2626' },
    { id: 2, left: '25%', top: '15%', size: 2, delay: 3.2, color: '#ef4444' },
    { id: 3, left: '80%', top: '30%', size: 4, delay: 2.8, color: '#f87171' },
    { id: 4, left: '5%', top: '70%', size: 2, delay: 3.5, color: '#dc2626' },
    { id: 5, left: '90%', top: '60%', size: 3, delay: 2.3, color: '#ef4444' },
    { id: 6, left: '60%', top: '10%', size: 3, delay: 4.1, color: '#f87171' },
    { id: 7, left: '15%', top: '80%', size: 2, delay: 2.7, color: '#dc2626' },
    { id: 8, left: '75%', top: '75%', size: 4, delay: 3.8, color: '#ef4444' },
    { id: 9, left: '40%', top: '5%', size: 2, delay: 2.9, color: '#f87171' },
    { id: 10, left: '95%', top: '40%', size: 3, delay: 3.3, color: '#dc2626' }
  ];

  return (
    <div className="min-h-screen flex items-center justify-center p-8 relative" style={{
      background: 'linear-gradient(135deg, #0c0c0c 0%, #1a1a1a 25%, #dc2626 50%, #991b1b 75%, #0f0f0f 100%)'
    }}>
      
      {/* Animated background particles - Only render on client */}
      {isClient && (
        <div className="absolute top-0 left-0 w-full h-full overflow-hidden" style={{ zIndex: 1 }}>
          {particles.map((particle) => (
            <div
              key={particle.id}
              className="absolute rounded-full opacity-60"
              style={{
                width: `${particle.size}px`,
                height: `${particle.size}px`,
                backgroundColor: particle.color,
                left: particle.left,
                top: particle.top,
                animation: `float ${particle.delay}s ease-in-out infinite alternate`
              }}
            />
          ))}
        </div>
      )}
      
      <div className="relative z-10 w-full max-w-2xl">
        <div className="bg-gradient-to-br from-gray-900/95 to-gray-800/95 backdrop-blur-xl border border-red-600/30 rounded-3xl shadow-2xl shadow-red-600/20">
          
          {/* Header */}
          <div className="text-center p-8 pb-4">
            <div className="text-6xl mb-4">🔥</div>
            <h2 className="text-4xl font-bold bg-gradient-to-r from-red-600 to-red-400 bg-clip-text text-transparent mb-2">
              สมัครสมาชิก
            </h2>
            <p className="text-gray-300">กรอกข้อมูลเพื่อสร้างบัญชีใหม่</p>
          </div>
          
          {/* Register Container */}
          <div className="px-8 pb-6">
            <div onSubmit={handleSubmit} className="space-y-6">
              
              {/* Username */}
              <div>
                <label className="block text-gray-200 font-semibold mb-2">
                  <i className="fas fa-user mr-2 text-red-600"></i>
                  ชื่อผู้ใช้
                </label>
                <input
                  type="text"
                  name="username"
                  className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 placeholder-gray-400 focus:border-red-600 focus:outline-none transition-all ${
                    errors.username ? 'border-red-500' : ''
                  }`}
                  value={formData.username}
                  onChange={handleChange}
                  placeholder="กรอกชื่อผู้ใช้"
                  disabled={isLoading}
                />
                {errors.username && <div className="text-red-400 text-sm mt-1">{errors.username}</div>}
              </div>

              {/* Password */}
              <div>
                <label className="block text-gray-200 font-semibold mb-2">
                  <i className="fas fa-lock mr-2 text-red-600"></i>
                  รหัสผ่าน <span className="text-red-400 text-sm">*จำเป็น</span>
                </label>
                <div className="relative">
                  <input
                    type={showPassword ? 'text' : 'password'}
                    name="password"
                    className={`w-full px-4 py-3 pr-12 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 placeholder-gray-400 focus:border-red-600 focus:outline-none transition-all ${
                      errors.password ? 'border-red-500' : ''
                    }`}
                    value={formData.password}
                    onChange={handleChange}
                    placeholder="กรอกรหัสผ่าน (อย่างน้อย 8 ตัวอักษร)"
                    disabled={isLoading}
                  />
                  <button
                    type="button"
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-red-600 hover:text-red-400"
                    onClick={() => setShowPassword(!showPassword)}
                    disabled={isLoading}
                  >
                    <i className={`fas ${showPassword ? 'fa-eye-slash' : 'fa-eye'}`}></i>
                  </button>
                </div>
                {errors.password && <div className="text-red-400 text-sm mt-1">{errors.password}</div>}
                
                {/* Password Strength Indicator */}
                {formData.password && (
                  <div className="mt-2">
                    <div className="flex justify-between items-center mb-1">
                      <span className="text-gray-400 text-sm">ความแข็งแกร่งของรหัสผ่าน:</span>
                      <span className="text-sm font-semibold" style={{ color: passwordStrength.color }}>
                        {passwordStrength.strengthText}
                      </span>
                    </div>
                    <div className="w-full bg-gray-800 rounded-full h-2">
                      <div 
                        className="h-2 rounded-full transition-all duration-300" 
                        style={{ 
                          width: `${(passwordStrength.score / 5) * 100}%`,
                          backgroundColor: passwordStrength.color
                        }}
                      />
                    </div>
                    <div className="text-gray-400 text-xs mt-1">
                      {passwordStrength.feedback}
                    </div>
                    {passwordStrength.score >= 3 && (
                      <div className="text-green-400 text-xs mt-1">
                        <i className="fas fa-check-circle mr-1"></i>
                        รหัสผ่านมีความปลอดภัยเพียงพอ
                      </div>
                    )}
                  </div>
                )}

                <div className="text-blue-400 text-xs mt-2">
                  <i className="fas fa-shield-alt mr-1"></i>
                  รหัสผ่านจะถูกเข้ารหัสอย่างปลอดภัยก่อนบันทึก
                </div>
              </div>

              {/* Name fields */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-gray-200 font-semibold mb-2">
                    <i className="fas fa-crown mr-2 text-red-600"></i>
                    คำนำหน้าชื่อ
                  </label>
                  <select
                    name="firstname"
                    className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 focus:border-red-600 focus:outline-none transition-all ${
                      errors.firstname ? 'border-red-500' : ''
                    }`}
                    value={formData.firstname}
                    onChange={handleChange}
                    disabled={isLoading}
                  >
                    <option value="">เลือก</option>
                    <option value="นาย">นาย</option>
                    <option value="นาง">นาง</option>
                    <option value="นางสาว">นางสาว</option>
                  </select>
                  {errors.firstname && <div className="text-red-400 text-sm mt-1">{errors.firstname}</div>}
                </div>

                <div>
                  <label className="block text-gray-200 font-semibold mb-2">ชื่อ</label>
                  <input
                    type="text"
                    name="fullname"
                    className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 placeholder-gray-400 focus:border-red-600 focus:outline-none transition-all ${
                      errors.fullname ? 'border-red-500' : ''
                    }`}
                    value={formData.fullname}
                    onChange={handleChange}
                    placeholder="ชื่อ"
                    disabled={isLoading}
                  />
                  {errors.fullname && <div className="text-red-400 text-sm mt-1">{errors.fullname}</div>}
                </div>

                <div>
                  <label className="block text-gray-200 font-semibold mb-2">นามสกุล</label>
                  <input
                    type="text"
                    name="lastname"
                    className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 placeholder-gray-400 focus:border-red-600 focus:outline-none transition-all ${
                      errors.lastname ? 'border-red-500' : ''
                    }`}
                    value={formData.lastname}
                    onChange={handleChange}
                    placeholder="นามสกุล"
                    disabled={isLoading}
                  />
                  {errors.lastname && <div className="text-red-400 text-sm mt-1">{errors.lastname}</div>}
                </div>
              </div>

              {/* Address */}
              <div>
                <label className="block text-gray-200 font-semibold mb-2">
                  <i className="fas fa-map-marker-alt mr-2 text-red-600"></i>
                  ที่อยู่
                </label>
                <textarea
                  name="address"
                  className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 placeholder-gray-400 focus:border-red-600 focus:outline-none transition-all resize-none ${
                    errors.address ? 'border-red-500' : ''
                  }`}
                  value={formData.address}
                  onChange={handleChange}
                  placeholder="กรอกที่อยู่"
                  rows="3"
                  disabled={isLoading}
                />
                {errors.address && <div className="text-red-400 text-sm mt-1">{errors.address}</div>}
              </div>

              {/* Sex and Birth Date */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-gray-200 font-semibold mb-2">
                    <i className="fas fa-venus-mars mr-2 text-red-600"></i>
                    เพศ
                  </label>
                  <div className="flex gap-6 mt-2">
                    <label className="flex items-center text-gray-200 cursor-pointer">
                      <input
                        type="radio"
                        name="sex"
                        value="male"
                        checked={formData.sex === 'male'}
                        onChange={handleChange}
                        disabled={isLoading}
                        className="mr-2 text-red-600 focus:ring-red-600"
                      />
                      <i className="fas fa-mars mr-1 text-blue-400"></i>
                      ชาย
                    </label>
                    <label className="flex items-center text-gray-200 cursor-pointer">
                      <input
                        type="radio"
                        name="sex"
                        value="female"
                        checked={formData.sex === 'female'}
                        onChange={handleChange}
                        disabled={isLoading}
                        className="mr-2 text-red-600 focus:ring-red-600"
                      />
                      <i className="fas fa-venus mr-1 text-pink-400"></i>
                      หญิง
                    </label>
                  </div>
                  {errors.sex && <div className="text-red-400 text-sm mt-1">{errors.sex}</div>}
                </div>
                
                <div>
                  <label className="block text-gray-200 font-semibold mb-2">
                    <i className="fas fa-birthday-cake mr-2 text-red-600"></i>
                    วันเกิด
                  </label>
                  <input
                    type="date"
                    name="birthday"
                    className={`w-full px-4 py-3 bg-gray-800/70 border-2 border-red-600/30 rounded-xl text-gray-200 focus:border-red-600 focus:outline-none transition-all ${
                      errors.birthday ? 'border-red-500' : ''
                    }`}
                    value={formData.birthday}
                    onChange={handleChange}
                    disabled={isLoading}
                    style={{ colorScheme: 'dark' }}
                  />
                  {errors.birthday && <div className="text-red-400 text-sm mt-1">{errors.birthday}</div>}
                </div>
              </div>

              {/* Terms and Conditions */}
              <div>
                <label className="flex items-center text-gray-200 cursor-pointer">
                  <input
                    type="checkbox"
                    name="acceptTerms"
                    checked={formData.acceptTerms}
                    onChange={handleChange}
                    disabled={isLoading}
                    className="mr-3 text-red-600 focus:ring-red-600 rounded"
                  />
                  <i className="fas fa-shield-alt mr-2 text-red-600"></i>
                  ฉันยอมรับ <a href="#" className="text-red-400 hover:text-red-300 underline">เงื่อนไขการใช้งาน</a> และ <a href="#" className="text-red-400 hover:text-red-300 underline">นโยบายความเป็นส่วนตัว</a>
                </label>
                {errors.acceptTerms && <div className="text-red-400 text-sm mt-1">{errors.acceptTerms}</div>}
              </div>

              {/* Security Notice */}
              <div className="bg-red-600/15 border border-red-600/30 rounded-xl p-4">
                <div className="flex items-start">
                  <i className="fas fa-info-circle text-red-600 mr-2 mt-0.5"></i>
                  <div className="text-gray-200 text-sm">
                    <strong>ความปลอดภัย:</strong> ข้อมูลทั้งหมดจะถูกเข้ารหัสก่อนจัดเก็บ และเราจะไม่เปิดเผยข้อมูลส่วนบุคคลของคุณแก่บุคคลที่สาม
                  </div>
                </div>
              </div>

              {/* Submit Button */}
              <button
                type="button"
                onClick={handleSubmit}
                className={`w-full py-4 px-6 rounded-xl font-bold text-white text-lg transition-all duration-300 ${
                  isLoading
                    ? 'bg-gray-600 cursor-not-allowed'
                    : 'bg-gradient-to-r from-red-600 to-red-700 hover:from-red-700 hover:to-red-800 hover:shadow-lg hover:shadow-red-600/25 transform hover:-translate-y-0.5'
                }`}
                disabled={isLoading}
              >
                {isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin mr-2"></i>
                    กำลังสมัครสมาชิก...
                  </>
                ) : (
                  <>
                    <i className="fas fa-user-plus mr-2"></i>
                    สมัครสมาชิก
                  </>
                )}
              </button>
            </div>
          </div>
          
          {/* Footer */}
          <div className="text-center p-6 border-t border-gray-700/50">
            <p className="text-gray-400">
              มีบัญชีอยู่แล้ว? <a href="/login" className="text-red-400 hover:text-red-300 font-semibold">เข้าสู่ระบบ</a>
            </p>
          </div>
        </div>
      </div>
      
      {/* External CSS */}
      <link 
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css" 
        rel="stylesheet" 
      />
      
      <style jsx>{`
        @keyframes float {
          from {
            transform: translateY(0px);
          }
          to {
            transform: translateY(-20px);
          }
        }
        
        input:focus, select:focus, textarea:focus {
          outline: none !important;
        }
        
        input::placeholder, textarea::placeholder {
          color: #9ca3af !important;
          opacity: 0.8;
        }
        
        select option {
          background-color: #1f2937;
          color: #f3f4f6;
        }
        
        input[type="date"]::-webkit-calendar-picker-indicator {
          filter: invert(1);
          cursor: pointer;
        }
      `}</style>
    </div>
  );
};

export default Register;